.aboutus {
  padding: 80px 0 100px 0;
  background: url(../img/aboutus.png) repeat center center;
  &__item {
    display: flex;
    flex-direction: column;
    flex: 0 1 33.313%;

    @include _992 {
      flex: 0 1 auto;
      align-items: center;
    }
  }

  &__item-descr {
    max-width: 350px;

    @include _575 {
      text-align: center;
      margin-bottom: 15px;
    }
  }

  &__categories {
    display: flex;
    flex-direction: column;
    flex: 0 1 33.313%;

    @include _992 {
      align-items: center;
      flex: 0 1 auto;
    }
  }

  &__categories-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 20px;

    @include _992 {
      flex-direction: column;
    }
  }

  &__categories-link {
    background: #0f0f0f;
    padding: 10px 20px;
  }

  &__subscribe {
    display: flex;
    flex-direction: column;
    flex: 0 1 33.313%;
    padding-top: 25px;

    @include _992 {
      flex: 0 1 auto;
      align-items: center;
    }
  }

  &__subscribe-descr {
    max-width: 350px;
    margin-bottom: 37px;

    @include _575 {
      text-align: center;
    }
  }

  &__input {
    color: $gray;
    max-width: 210px;
    padding: 10px 0 10px 15px;
    &:focus {
      outline: none;
    }
  }

  &__button {
    padding: 0 20px;
    background: #0f0f0f;
    color: $whiteColor;
    text-transform: uppercase;
  }
}

.aboutus-container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include _992 {
    flex-direction: column;
    justify-content: center;
  }
}
.aboutus-title {
  font-family: $font-playfair;
  font-size: 24px;
  text-transform: uppercase;
  color: $whiteColor;
  margin-bottom: 40px;
}
.form {
  display: flex;
}
